import $ from 'jquery'

const NAME                = 'alert'
const VERSION             = '0.5.2'

const DATA_KEY            = 'sc.alert'
// アラート閉じた時のイベントとかに使うみたい
// http://bootstrap3.cyberlab.info/javascript/alerts-events.html
const EVENT_KEY           = `.${DATA_KEY}`
// イベントを無効にする用
// https://getbootstrap.jp/docs/4.1/getting-started/javascript/
const DATA_API_KEY        = '.data-api'
// 他のフレームワークと名前衝突を回避する用
const JQUERY_NO_CONFLICT  = $.fn[NAME]

// アラート消す用
const Selector = {
  DISMISS : '[data-dismiss="alert"]'
}

// イベント用の名前
// e.g. close.sc.alert
const Event = {
  CLOSE          : `close${EVENT_KEY}`,
  CLOSED         : `closed${EVENT_KEY}`,
  CLICK_DATA_API : `click${EVENT_KEY}${DATA_API_KEY}`
}

// htmlのクラス名
const ClassName = {
  ALERT : 'alert',
  FADE  : 'fade',
  SHOW  : 'show'
}

class Alert {
  constructor(element) {
    this._element = element
  }

  // バージョンのゲッター
  static get VERSION() {
    return VERSION
  }

  close(element) {
    // コンストラクタで取得したelement
    let rootElement = this._element

    // closeの引数があったら_getRootElementを発動！
    // class="close"の指定があったときかな
    if (element) {
      rootElement = this._getRootElement(element)
    }

    const customEvent = this._triggerCloseEvent(rootElement)

    if (customEvent.isDefaultPrevented()) {
      return
    }

    this._removeElement(rootElement)
  }

}

export default Alert
